h1, h2, p {
  padding: 0;
  margin: 0;
  border: 0;
}

.ReactModal__Overlay {
  z-index: 100;
}

.button {
  color: white;
  background-color: rgb(55, 19, 213);
  margin: 0.5rem;
  text-decoration: none;
  
  cursor: pointer;
  user-select: none;
  border-radius: 0.5rem;
  min-width: 5rem;
  padding: 0.7em;
  height: 0.8rem;
  display: flex;
  justify-content: center;
  align-items: center;
}