.product-container {
    position: relative;
    width: 300px;
    height: 300px;
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    text-align: center;
}

.heart-icon {
    position: absolute;
    top: 5px;
    right: 10px;
    font-size: 24px;
    color: #ff4d4d;
    cursor: pointer;
}

.product-title {
    position: absolute;
    font-size: 24px;
    color: white;
    font-weight: bold;
    z-index: 2;
    padding: 5px 10px;
    border-radius: 5px; 
}

.product-image img {
    width: 100%;
    height: auto;
}

.view-count {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    width: 90%; /* Adjust width as needed */
    font-size: 14px;
    color: white;
}

.icon-right {
    font-size: 18px;
    cursor: pointer;
}

.product-modal {
    background-color: #2e302e;
    border-radius: 8px;
    padding: 20px;
    max-width: 500px;
    margin: 4rem auto;
    color: white;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.product-modal h2 {
    margin-top: 0;
}

.product-modal iframe {
    border: none;
    border-radius: 8px;
    margin-top: 10px;
}

.product-modal button {
    margin-top: 20px;
    padding: 10px 20px;
    border: none;
    background-color: #007bff;
    color: white;
    cursor: pointer;
    border-radius: 4px;
}

.canvas {
    background-color: white;
    margin-top: 1rem;
}
